.search__excercise {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 4rem;
  align-items: center;
  gap: 2rem;
}
.search__excercise span {
  font-size: 5rem;
  color: #fff;
  font-weight: bold;
  margin-bottom: 4rem;
  text-align: center;
}
.search__excercise input {
  width: 35%;
  height: 35px;
  padding: 5px;
  border: none;
  border-radius: 5px;
}

@media only screen and (max-width: 1200px) {
  .search__excercise input {
    width: 60%;
  }
}
@media only screen and (max-width: 950px) {
  .search__excercise span {
    font-size: 4rem;
    margin-bottom: 2rem;
  }
}

@media only screen and (max-width: 720px) {
  .search__excercise input {
    width: 65%;
    height:30px;
  }

  .search__excercise span {
    font-size: 3rem;
    margin-bottom: 1rem;
  }
  .search__excercise{
    margin: 4rem 1rem;
  }
}
@media only screen and (max-width: 470px) {
    .search__excercise span {
        font-size: 2.5rem;
      
      }

}
@media only screen and (max-width: 390px) {
    .search__excercise span {
        font-size: 1.9rem;
      
      }

}