
.hero__excercise{
    display:grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom:2em;
    background:#464c3a;
   
}
.hero__excercise__left{
    display:flex;
    flex-direction:column;
    color:#fff;
    justify-content:center;
    margin:2rem;
   


}
.hero__excercise__dec{
color:rgb(204, 146, 0);
}
.hero__excercise__left span{
    font-size:4rem;
    font-weight:bold;
    
}
.hero__excercise__right{
    background-image:url(../../Assets/images/heroexcercise.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width:100%;
    height:90vh;
}
.hero__excercise__left p {
    font-size:1.2rem;
    margin-top:2rem;
    width:70%;
    font-weight:bold;
    line-height: 1.6;
}
@media only screen and (max-width: 1360px) {

    .hero__excercise__left span{
        font-size:3.4rem;
    }
}
@media only screen and (max-width: 1160px) {
    .hero__excercise{
        grid-template-columns: 1.3fr 1fr;
    }
    .hero__excercise__left span{
        font-size:3.2rem;
    }
    .hero__excercise__left p{
        font-size:1rem;
    }
    .hero__excercise__left{
        margin:2rem 1rem;
    }
   
}
@media only screen and (max-width: 920px) {
    .hero__excercise__left span{
        font-size:2.5rem;
    }
    .hero__excercise__left p{
        width:100%;
    }
}
@media only screen and (max-width: 520px){
    .hero__excercise{
        display:flex;
        flex-direction: column-reverse;
    }
    .hero__excercise__right{
        height:70vh;
    }
    .hero__excercise__left p{
        margin-top:1rem;
    }
}

@media only screen and (max-width: 410px){
.hero__excercise__left span{
    font-size:1.9rem;
}
.hero__excercise__right{
    height:50vh;
}
}