.search__meals__container{
    background-image: url(../../Assets/images/heromeal.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height:50vh;
    display:flex;
    align-items:center;
    justify-content: center;

}
.meal__form{
    display:flex;
    gap:1rem;
    justify-content:center;
    width:100%;
    align-items:center;
    
}
.meal__input{
    padding-left:10px;
    width:40%;
    text-transform: capitalize;
    border-radius:5px;
    border:none;
    height:2rem;
}
.search__buttons{
    display:flex;
    gap:10px;
    width:40%;
}
.btn:focus {
    outline: 0;
    box-shadow: none;
    background:#000;
  }

  @media only screen and (max-width: 500px){

    .meal__form{
        display:flex;
        flex-direction:column;

    }
    .meal__input{
        width:90%;
    }
    .search__buttons{
        width: 100%;
        justify-content: center;
    }
  }