.exercise__card{
    background:#fff;
    color:#000;
    border-radius:10px;
}
.exercise__card img{
    border-radius:10px;
}
.exercise__card__bottom{
    display:flex;
    gap:2rem;
    padding:10px;
}
.exercise__card__bottom span{
    padding:10px;
    background:red;
    color:#fff;
    border-radius:5px;
    font-weight:600;
}
.exercise__card__bottom span:nth-child(2){
    background:orange;
}
.exercise__card__name{
    padding:10px;
}
.exercise__card__name span{
    text-transform:capitalize;
    font-weight:600;
}