.meals__container {
  display: flex;
  flex-wrap: wrap;
  gap: 1em;
  align-items: center;
  justify-content: center;
  padding-top: 5rem;
  margin-bottom: 2rem;
}
.meals__title {
  color: #fff;
  text-align: center;
  padding: 2rem;
  font-size: 2.5rem;
}
.meals__card {
  background: #fff;
  margin-bottom: 2rem;
  transition: 0.5s ease-in-out;
  cursor: pointer;
}
.meals__card:hover {
  transform: scale(1.05);
}
.meals__img {
  width: 270px;
}
.meals__card__bottom {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  font-size: 2rem;
  padding: 0.5rem 10px;
  gap: 1rem;
  max-width: 370px;
  height: 80px;
}
.meals__card__bottom h5 {
  font-size: 1.5rem;
}

.like {
  background: none !important;
  border: none !important;
}
.like svg {
  width: 30px;
  height: 30px;
  transition: 0.5s ease-in-out;
  cursor: pointer;
}
.like svg:hover {
  fill: red;
}
.no__match {
  font-size: 4rem;
  margin: 4rem auto;
  color: #fff;
  text-align: center;
}
@media only screen and (max-width: 560px){
    .meals__title{
        font-size:1.6rem;
        padding:1rem 0;
    }
    .meals__card{
        width:90%;
        margin:0 auto;
    }
    .meals__img{
        width:100%;
    }
    .meals__card__bottom {
        height:40px;
    }
    .meals__card__bottom h5 {
  font-size: 1rem;
}
}