.exercise__details{
    display:grid;
    grid-template-columns:1fr 1fr;
    gap:1em;
    margin:4em auto;
    justify-content: center;
    align-items:center;
    max-width:1200px;
    padding:0 2rem;

}
.exercise__details__right{
    display:flex;
    flex-direction:column;
    color:#fff;
    gap:1rem;
}
.exercise__details__right h1{
    font-size:3rem;
    text-transform: capitalize;
    color:#c5ff38;
    
    
}
.exercise__details__right span{
    font-size:1.6rem;
}
.exercise__details__icons{
    display:flex;
    margin-top:4rem;
    justify-content:space-between;
    align-items:center;
}
.exercise__icon{
    width:40px;
}
.exercise__details__col{
    display:flex;
    flex-direction:column;
    align-items:center;
}
.exercise__details__col span:nth-child(2){
    text-transform: capitalize;
}
.detail__image{
    border-radius:10px;
}
@media only screen and (max-width: 1100px) {
    .exercise__details__right span{
        font-size:1.4rem;
    }

}
@media only screen and (max-width: 770px) {
    .exercise__details__right h1 , .exercise__video__container h1{
        font-size:2rem;
    }
    
    .exercise__details{
        display:flex;
        flex-direction:column;
        
    }
    .exercise__details__icons{
        margin-top:1rem;
        justify-content:space-around;
    }
}
@media only screen and (max-width: 580px){
    .exercise__video__container h1{
        font-size:1.8rem;
    }
    .exercise__details__col span:nth-child(2){
        font-size:1rem;
}
.exercise__details__right span {
    font-size:1.2rem;
}
.exercise__details{
    padding: 0 1rem;
    
}
}
@media only screen and (max-width: 380px){
    .detail__image{
        width:100%;
    }
}