.exercises__body h2 {
    font-size:5rem;
    color:#fff;
    text-align:center;
    margin-bottom:4rem;
}
.exercises__body__container{
    display:flex;
    justify-content:center;
    flex-wrap:wrap;
    color:#fff;
    gap:100px;
}

.exercise__pagination{
    margin:4rem 0;
}
.pagination {
    justify-content: center;
    display: flex;
    padding-left: 0;
    list-style: none;
  }
  
  .page-item .page-link {
    position: relative;
    display: block;
    margin: 0 5px;
    min-height: 40px;
    min-width: 40px;
    border-radius: 20px;
    text-align: center;
    line-height: 40px;
    color: #fff;
    text-decoration: none;
    font-size:1.2rem;
  }
  
  .page-item a.page-link:hover {
    background-color: #cccccc;
  }
  
  .page-item.active .page-link {
    font-weight: 700;
    color: #ffffff;
    background:linear-gradient(175deg, #4c572c 1.14%, #232323 100.75%);
  }
  
  .page-item.disabled .page-link {
    color: #6c757d;
    pointer-events: none;
    cursor: auto;
  }
  
  .sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border: 0;
  }
  @media only screen and (max-width: 950px) {
    .exercises__body h2{
      font-size:4rem;
    }
  }
  @media only screen and (max-width: 950px) {
    .exercises__body h2 {
      font-size: 3rem;
    }
  }
  @media only screen and (max-width: 470px){
    .exercises__body h2 {
      font-size: 2.5rem;
    }
  }
  @media only screen and (max-width: 390px){
    .exercises__body h2 {
      font-size: 1.9rem;
    }
  }