.favorite__meals__container{
    color:#fff;
    margin-top:2rem;
    margin:3rem auto;
    width:90%;
    
    
}
.favorite__meals h5{
    font-size:3rem; 
    margin:1rem;
    text-align:center;
}
.favorites__meals__box{
    display: flex;
    gap: 20px;
    margin: 0 1rem;
    align-items:center;
    flex-wrap: wrap;
}
.favorite__meals__item{
    display:flex;
    flex-direction:column;
    gap:20px;
    width:80px;
    align-items:center;
    font-weight:bold;
    

}

.favorite__meals__img{
    width: 80px;
    height: 80px;
    border-radius: 50px;
    border: 2px solid white;
    cursor: pointer;

}

.remove__btn{
  align-items: center;
  background-color: #FFFFFF;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: .25rem;
  box-shadow: rgba(0, 0, 0, 0.02) 0 1px 3px 0;
  box-sizing: border-box;
  color: rgba(0, 0, 0, 0.85);
  cursor: pointer;
  display: inline-flex;
  font-size: 12px;
  font-weight: 600;
  justify-content: center;
  margin: 0;
  min-height: 1.2rem;
  padding: 2px 5px;
  position: relative;
  text-decoration: none;
  transition: all 250ms;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: baseline;
  width: auto;
}

.remove__btn:hover,
.remove__btn:focus {
  border-color: rgba(0, 0, 0, 0.15);
  box-shadow: rgba(0, 0, 0, 0.1) 0 4px 12px;
  color: rgba(0, 0, 0, 0.65);
}

.remove__btn:hover {
  transform: translateY(-1px);
}

.remove__btn:active {
  background-color: #F0F0F1;
  border-color: rgba(0, 0, 0, 0.15);
  box-shadow: rgba(0, 0, 0, 0.06) 0 2px 4px;
  color: rgba(0, 0, 0, 0.65);
  transform: translateY(0);
}
@media only screen and (max-width: 500px){
  .favorite__meals__img {
    width: 50px;
    height: 50px;
  }
  .favorite__meals__item {
    width: 50px;
  }
}