.exercise__horizontal{
    display:flex;
    gap:1em;
    color:#fff;
    overflow:hidden;
}
.exercise__category__card{
    display:flex;
    flex-direction: column;
    color: #fff;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;
    border-radius: 10px;
    padding: 0.5rem;
    background: linear-gradient(175deg, #4c572c 1.14%, #232323 100.75%);
    width:100%;
    height:400px;
    transition:0.5s ease-in-out;
    overflow:hidden;
    cursor:pointer;
}
.exercise__category__card:hover{
  transform: scale(1.03);
}
.exercise__category__card span{ 
  text-transform:capitalize;
  font-size:2rem;
}
.excercise__category__card__top img{
  width:220px!important;
  height:290px!important;
  object-fit:contain!important;
}
.excercise__category__card__bottom{
  margin-top:1rem;
}

  .swiper {
    width: 100%;
    height: 100%;
   
  }
  
  .swiper-slide {
    text-align: center;
    font-size: 18px;
  
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }
  
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .swiper-pagination-bullet-active {
    background:chartreuse!important;
  }
  .swiper-free-mode>.swiper-wrapper{
    margin:2rem auto !important;
  }
  @media only screen and (max-width: 500px) {
    .swiper-slide {
      margin:5px !important;
      width:33%!important;
    }
    .exercise__category__card{
      padding:0;
      height:350px;
    }
    .exercise__category__card span{
      font-size:1.5rem;
    }
    .excercise__category__card__top img{
      height:250px!important;
    }
  
  }
  @media only screen and (max-width: 420px) {
  
    .swiper-slide {
      margin:1px !important;
      width:35%!important;
    }
  
  .exercise__category__card span {
    font-size: 1rem;
}
}
