.contact__hero{
    background-image: url(../../Assets/images/contact__hero.jpeg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height:80vh;
    width:100%

}
.contact__content{
    height:40vh;
    padding:7rem 0;
    color:#fff;
    width:90%;
    margin:0 auto;
    display:grid;
    grid-template-columns: repeat(3,1fr);
    
}
.contact__col{
    display: flex;
    flex-direction: column;
    gap: 10px;
    justify-content: flex-start;
    border: 1px solid white;
    padding: 2rem 10px;
}
.contact__col:nth-child(2) , .contact__col:nth-child(3){
    justify-content:center;
}

.contact__col h1{
    font-size:3.5rem;
    color:rgb(148, 253, 43);
}
.contact__hours{
    color:rgb(148, 253, 43);
}
.contact__col p{
    font-size:1.6rem;
}
.contact__col span{
    font-size:2rem;
}
.contact__col span svg{
    padding-right:10px;
    color:rgb(148, 253, 43);
}
.contact__map{
    padding-bottom: 2rem;;
}
@media only screen and (max-width: 1180px){
    .contact__content{
        width:98%;
    }
    .contact__col p {
        font-size: 1.4rem;
    }
    .contact__col span{
        font-size:1.6rem;
    }
    .contact__hours{
        font-size:2rem!important;
    }
}
@media only screen and (max-width: 895px){
    .contact__col span{
        font-size:1.4rem;
    }
    .contact__col p {
        font-size: 1.2rem;
    }
    .contact__col h1{
        font-size:3rem;
    }
}
@media only screen and (max-width: 770px){
    .contact__col span{
        font-size:1.2rem;
    }
    .contact__hours {
        font-size:1.8rem!important;
    }
}
@media only screen and (max-width: 680px){
    .contact__hero{
        height:55vh;
    }
    .contact__content{
        display:flex;
        flex-direction:column;
        height:100%;
    }
    .contact__col span{
        font-size:1.8rem;
    }
    .contact__hours {
        font-size: 2rem!important;
    }
}
@media only screen and (max-width: 680px){
    .contact__col span{
        font-size:1.5rem;
    }
}