.exercise__video__container{
    padding:2rem 2rem 8rem 2rem;
    display:flex;
    flex-direction: column;
    gap:3rem;
}
.exercise__video__container h1{
    color:#fff;
    font-size:2.5rem;
}
.exercise__video__container span{
    color:#c5ff38;
    text-transform: capitalize;

}


.exercise__video{
    display:flex;
    justify-content:space-between;
    gap:1rem;

}
.video__card img{
width:100%;
height:100%;
}
.video__card{
    display:flex;
    flex-direction:column;
    gap:10px;
    background:white;
    position:relative;
    width:100%;
    justify-content: center;
    padding-bottom: 30px;
    transition:0.5s ease-in-out; 
   
}
.video__card:hover{
    transform:scale(1.01);
  
}


.video__card p{
    color:#000;
    font-weight:bold;
    padding-left:5px;
    
    
    
}

@media only screen and (max-width: 700px){
    .video__card p{
        font-size:12px;
    }
}

@media only screen and (max-width: 560px){
    .exercise__video__container{
        padding:2rem 10px 8rem 10px;
    }
    .exercise__video{
        gap:0.5rem;
    }
}